@font-face {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/robotocondensed-regular.woff2") format("woff2"), url("../fonts/robotocondensed-regular.woff") format("woff"); }

@font-face {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: url("../fonts/robotocondensed-bold.woff2") format("woff2"), url("../fonts/robotocondensed-bold.woff") format("woff"); }

@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src: url("../fonts/roboto-medium.woff2") format("woff2"), url("../fonts/roboto-medium.woff") format("woff"); }

@font-face {
  font-family: "PT Mono";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/ptmono-regular.woff2") format("woff2"), url("../fonts/ptmono-regular.woff") format("woff"); }

/* Global */
body {
  margin: 0;
  padding: 0;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  overflow-x: hidden; }

.body--overflow-hidden {
  overflow: hidden; }

a {
  color: inherit;
  background-color: inherit;
  text-decoration: none; }

img {
  max-width: 100%;
  height: auto; }

*,
*::before,
*::after {
  box-sizing: border-box; }

.container {
  position: relative;
  margin: 0 auto;
  max-width: 1417px;
  min-width: 320px; }
  @media (max-width: 1023px) {
    .container {
      max-width: 715px; } }
  @media (max-width: 767px) {
    .container {
      max-width: 290px; } }

.main-header {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 950px;
  color: #ffffff;
  background-color: #000000;
  background-image: url("../img/background_desktop@1x.jpg");
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover; }
  @media (min-resolution: 1.5dppx) {
    .main-header {
      background-image: url("../img/background_desktop@2x.jpg"); } }
  @media (max-width: 1023px) {
    .main-header {
      display: block;
      height: 347px;
      background-image: url("../img/background_tablet@1x.jpg"); } }
  @media (max-width: 1023px) and (min-resolution: 1.5dppx) {
    .main-header {
      background-image: url("../img/background_tablet@2x.jpg"); } }
  @media (max-width: 767px) {
    .main-header {
      height: 396px;
      background-image: url("../img/background_mobile@1x.jpg"); } }
  @media (max-width: 767px) and (min-resolution: 1.5dppx) {
    .main-header {
      background-image: url("../img/background_mobile@2x.jpg"); } }

.main-header__wrap {
  margin-top: 32px;
  display: flex;
  justify-content: space-between;
  align-items: center; }
  @media (max-width: 1023px) {
    .main-header__wrap {
      margin-top: 20px; } }
  @media (max-width: 767px) {
    .main-header__wrap {
      margin-top: 17px; } }

.main-header__link {
  display: flex;
  align-items: flex-start; }
  .main-header__link svg {
    margin-right: 16px; }
  .main-header__link h1 {
    margin-top: 6px;
    margin-bottom: 0;
    font-size: 25px;
    font-weight: 700;
    line-height: 29px; }
  .main-header__link p {
    margin-top: 0;
    margin-bottom: 0;
    color: #788891;
    font-family: "Roboto", "Arial", serif;
    font-size: 12px;
    font-weight: 500;
    line-height: 14px; }
  @media (max-width: 767px) {
    .main-header__link h1,
    .main-header__link p {
      display: none; } }

.main-header__wrap-right {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 654px; }
  @media (max-width: 1023px) {
    .main-header__wrap-right {
      width: 88px; } }

.main-header__post,
.main-header__telephone {
  font-family: "PT Mono", "Arial", serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 18px; }
  .main-header__post p,
  .main-header__telephone p {
    margin: 0; }
    @media (max-width: 1023px) {
      .main-header__post p,
      .main-header__telephone p {
        display: none; } }

.main-header__post {
  position: relative;
  margin-right: 122px; }
  @media (max-width: 1023px) {
    .main-header__post {
      margin-right: 33px; } }
  .main-header__post::after {
    content: "";
    position: absolute;
    left: -40px;
    top: 0;
    width: 19px;
    height: 14px;
    background-image: url("../img/envelope.svg"); }

.main-header__telephone {
  position: relative;
  margin-right: 38px; }
  .main-header__telephone p {
    width: 184px; }
  .main-header__telephone::after {
    content: "";
    position: absolute;
    left: -40px;
    top: 0;
    width: 18px;
    height: 18px;
    background-image: url("../img/phone-call.svg"); }

.main-header__button {
  height: 45px;
  width: 180px;
  border: 1px solid #ffffff;
  border-radius: 5px;
  color: #ffffff;
  background: transparent;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-transform: uppercase; }
  @media (max-width: 1023px) {
    .main-header__button {
      display: none; } }
